.demandAndSupplyTable {
  table {
    thead {
      tr {
        th {
          vertical-align: middle;
        }
        &:nth-of-type(1) {
          th {
            &:nth-of-type(2) {
              position: sticky;
            }
          }
        }
        &:nth-of-type(2) {
          th {
            &:nth-of-type(1) {
              position: sticky;
              z-index: 5;
            }
            &:nth-of-type(2) {
              position: sticky;
              z-index: 5;
            }
          }
        }
      }
    }
    tbody {
      tr {
        &:hover {
          td {
            background-color: #d4e7fb;
          }
        }
        td {
          background-color: #d4e7fb;
          &:has(.even) {
            background-color: #facbcb;
          }
          &:has(.odd) {
            background-color: #c5e8e8;
          }
        }
        &:nth-of-type(1) {
          td {
            background-color: #f5d5a7 !important;
          }
        }
      }
    }
  }
}
